.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.img-card {
  border-radius: 4px;
  background: #f5f5f5;
  color: #666;
  overflow: hidden;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
}
.img-card:hover {
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
}
.img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}
.card-body {
  padding: 15px;
}
 
.card-title {
  font-size: 20px;
  font-weight: 600;
}
 
.card-text {
  margin-top: 10px;
}
 
/* card style end */
 
 
/* carousel slider style start */
 
.slick-slide > div {
  margin: 0 10px;
}
 
.slick-list {
  margin: 0 -10px;
}
 
.slick-slide *:focus{
  outline: none;
}
/* carousel slider style end */


